<template>
  <div class="growth-classroom-tab-container">

    <div class="tabs-box f-row-between-c">
      <div
        v-for="(item, index) in tags"
        :key="index"
        @click="clickTab(index)"
        :class="['tab', activeTab === index ? 'active' : '']"
      >
        {{ item }}
        <div v-show="activeTab === index" class="bar" />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Tab, Tabs } from 'vant';

Vue.use(Tab).use(Tabs);
export default {
  props: {
    tags: {
      type: Array,
      default: ['推荐', '分类1', '分类2']
    }
  },

  data() {
    return {
      activeTab: 0
    }
  },

  created() {
    document.title = '我的发布';
  },

  methods: {
    clickTab(index) {
      this.activeTab = index;
      this.$emit( 'clickTab', index );
    }
  }
}
</script>

<style lang="scss" scoped>
.growth-classroom-tab-container {
  width: 100%;
  height: 45px;
  border-bottom: #EEEEEE 1px solid;

  .tabs-box {
    width: 100%;
    padding: 0 47px;
    box-sizing: border-box;
    height: 45px;

    .tab {
      font-size: 15px;
      color: rgba(102,102,102,1);

      .bar {
        width: 30px;
        height: 3px;
        background: rgba(244,186,65,1);
        border-radius: 1px;
        position: relative;
        bottom: -10px;
        left: calc(50% - 15px);
      }
    }
    .active {
      font-size: 16px;
      font-weight: bold;
      color: rgba(244,186,65,1);
    }
  }
  .tabs-box::-webkit-scrollbar {
    display: none;
  }
}
</style>


