<template>
  <div class="my-publish-container">
    
    <MyPublishTab
      :tags="tags"
      @clickTab="clickTab"
    />

    <div class="message-area">

      <div v-if="tags[activeIndex] === '我的留言'">
        <van-list
          v-model="myLeaveMessage.loading"
          :finished="myLeaveMessage.finished"
          finished-text="没有更多了"
          @load="getMyLeaveMessage"
        >
          <MessageList
            :isShowReply="false"
            :messageList="myLeaveMessage.list"
            @switchPraise="switchPraiseMyMessage"
          />
        </van-list>
      </div>

      <div v-else-if="tags[activeIndex] === '我的评论'">
        <van-list
          v-model="myComment.loading"
          :finished="myComment.finished"
          finished-text="没有更多了"
          @load="getMyComment"
        >
          <MessageList
            :isShowReply="false"
            :isShowReplyLink="true"
            :isRenderComment="true"
            :messageList="myComment.list"
            @switchPraise="switchPraiseMyComment"
          />
        </van-list>
      </div>

      <div v-else-if="tags[activeIndex] === '网友回复'">
        <van-list
          v-model="userRes.loading"
          :finished="userRes.finished"
          finished-text="没有更多了"
          @load="getUserRes"
        >
          <MessageList
            :isShowReply="false"
            :isShowReplyLink="true"
            :isRenderComment="true"
            :messageList="userRes.list"
            @switchPraise="switchPraiseUserRes"
          />
        </van-list>
      </div>

    </div>

  </div>
</template>

<script>
import MyPublishTab from '@/components/myPublish/MyPublishTab.vue';
import MessageList from '@/components/message/MessageList.vue';
import { apiUserLeaveMessageList } from '@/service/api/leaveMessage';
import { apiCommnetListByUser, apiCommentListByUserRes } from '@/service/api/comment';
import Vue from 'vue';
import { Toast, List } from 'vant';

Vue.use(Toast).use(List);

export default {
  components: {
    MyPublishTab,
    MessageList
  },

  data() {
    return {
      tags: [ '我的留言', '我的评论', '网友回复' ],
      activeIndex: 0,

      myLeaveMessage: {
        list:[],
        finished: true,
        loading: false,
        pageNum: 0,
        pageSize: 20
      },

      myComment: {
        list:[],
        finished: true,
        loading: false,
        pageNum: 0,
        pageSize: 20
      },

      userRes: {
        list:[],
        finished: true,
        loading: false,
        pageNum: 0,
        pageSize: 20
      }
    }
  },

  mounted() {
    this.getMyLeaveMessage();
    this.getMyComment();
    this.getUserRes();
  },

  methods: {
    // todo 点赞状态切换的伪处理(不请求接口)
    switchPraiseMyMessage(messageIndex) {
      this.myLeaveMessage.list[ messageIndex ].isPraise = !this.myLeaveMessage.list[ messageIndex ].isPraise;
      this.myLeaveMessage.list[ messageIndex ].praiseUv = this.myLeaveMessage.list[ messageIndex ].isPraise ? this.myLeaveMessage.list[ messageIndex ].praiseUv + 1 : this.myLeaveMessage.list[ messageIndex ].praiseUv - 1;
    },
    switchPraiseMyComment(messageIndex) {
      this.myComment.list[ messageIndex ].isPraise = !this.myComment.list[ messageIndex ].isPraise;
      this.myComment.list[ messageIndex ].praiseUv = this.myComment.list[ messageIndex ].isPraise ? this.myComment.list[ messageIndex ].praiseUv + 1 : this.myComment.list[ messageIndex ].praiseUv - 1;
    },
    switchPraiseUserRes(messageIndex) {
      this.userRes.list[ messageIndex ].isPraise = !this.userRes.list[ messageIndex ].isPraise;
      this.userRes.list[ messageIndex ].praiseUv = this.userRes.list[ messageIndex ].isPraise ? this.userRes.list[ messageIndex ].praiseUv + 1 : this.userRes.list[ messageIndex ].praiseUv - 1;
    },

    // todo 获取网友回复
    async getUserRes() {
      const userResResponse = await apiCommentListByUserRes(this.userRes.pageNum, this.userRes.pageSize);
      if (!userResResponse || userResResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.userRes.list.push( ...userResResponse.data );
      if (userResResponse.data.length < this.userRes.pageSize) {
        this.userRes.finished = true;
      } else {
        this.userRes.pageNum += 1;
        this.userRes.finished = false;
      }
      this.userRes.loading = false;
    },

    // todo 获取我的评论
    async getMyComment() {
      const myCommentResponse = await apiCommnetListByUser(this.myComment.pageNum, this.myComment.pageSize);
      if (!myCommentResponse || myCommentResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.myComment.list.push( ...myCommentResponse.data );
      if (myCommentResponse.data.length < this.myComment.pageSize) {
        this.myComment.finished = true;
      } else {
        this.myComment.pageNum += 1;
        this.myComment.finished = false;
      }
      this.myComment.loading = false;
    },

    // todo 获取我的留言
    async getMyLeaveMessage() {
      const myLeaveMessageResponse = await apiUserLeaveMessageList(this.myLeaveMessage.pageNum, this.myLeaveMessage.pageSize);
      if (!myLeaveMessageResponse || myLeaveMessageResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.myLeaveMessage.list.push( ...myLeaveMessageResponse.data );
      if (myLeaveMessageResponse.data.length < this.myLeaveMessage.pageSize) {
        this.myLeaveMessage.finished = true;
      } else {
        this.myLeaveMessage.pageNum += 1;
        this.myLeaveMessage.finished = false;
      }
      this.myLeaveMessage.loading = false;
    },

    // todo 切换 tab 标签
    clickTab(clickIndex) {
      this.activeIndex = clickIndex;
    }
  }
}
</script>

<style lang="scss" scoped>
.my-publish-container {
  width: 100%;
  background-color: #fff;

  .message-area {
    width: 100%;
    padding: 20px 20px 0;
    box-sizing: border-box;
  }
}
</style>


